.todo-item-container {
	background-color: white;
	color: #3a3131;
	/* padding: 1.5em 0.8em; */
	padding: 1em 0.8em;
	display: flex;
	justify-content: space-between;
	min-height: 27px;
	padding-right: 0;
}

.task-info:hover {
	cursor: pointer;
}

.task-info {
	width: 100%;
}

.item-bar {
	display: flex;
	min-height: 27px;
	width: 3px;
	border-radius: 1.5px;
	/* margin-left: 3px; */
}

.active-item-bar {
	display: flex;
	min-height: 27px;
	width: 3px;
	border-radius: 1.5px;
	background: black;
}

.todo-item-container:hover > .item-bar {
	background: rgb(168, 168, 168);
}

.todo-item-note-container {
	box-sizing: border-box;
	background-color: rgb(255, 252, 213);
	display: flex;
	padding: 1em 0.8em;
	margin-top: 0.5em;
	width: 100%;
	border-radius: 5px;
	white-space: pre-wrap;
}

.todo-item-container-left {
	display: flex;
	width: 90%;
}

.todo-item-complete-button {
	border: 0;
	background: none;
	cursor: pointer;
	margin-right: 2px;
	height: 36px;
}

/* bad for a11y but looks better ðŸ¤·â€ */
/* good fix: https://medium.freecodecamp.org/how-to-make-your-fancy-svg-button-accessible-83c9172c3c15 */
.todo-item-complete-button:focus {
	outline: 0;
}

.todo-item-circle {
	border-radius: 100%;
	width: 24px;
	height: 24px;
}

.todo-item-circle-empty {
	box-sizing: border-box;
	border: 2px solid #a2adc1;
}

/* .todo-item-circle-empty:hover {
	background: #a2adc12e;
} */

.todo-item-circle-check {
	background: #64e232;
	color: white;

	/*
    center children vertically and horizontally
    https://stackoverflow.com/questions/25832340/css-flexbox-vertically-horizontally-center-image-without-explicitely-defining-pa
  */
	display: flex;
	justify-content: center; /* align horizontal */
	align-items: center; /* align vertical */
}

.todo-item-circle-check > svg {
	width: 15px;
	height: 15px;
}

.todo-item-task {
	margin-bottom: 8px;
	font-weight: 550;
}

.todo-item-start-session-button {
	display: block;
	outline: 0;
	color: #a2adc1;
	border: 0;
	background: none;
	cursor: pointer;
	height: 36px;
	/* display: none; */
}

.todo-item-start-session-button > svg {
	width: 24px;
	height: 24px;
}

/* .todo-item-start-session-button:hover > svg {
	width: 25px;
	height: 25px;
} */

/* bad for a11y but looks better ðŸ¤·â€ */
/* good fix: https://medium.freecodecamp.org/how-to-make-your-fancy-svg-button-accessible-83c9172c3c15 */
/* .todo-item-start-session-button:focus {
	outline: 0;
} */

.todo-item-container:hover .todo-item-start-session-button {
	display: block;
}
