body {
  margin: 0;
  padding: 0;
  font-family: "Inter", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F7FAFF;
  color: #2E353F;

}

.timer-container {
	font-weight: 700;
	font-size: 2rem;
	color: white;
	padding: 1em 1em;
	display: flex;
	flex-wrap: wrap;
	text-align: center;
}

.timer-container > div {
	flex-grow: 1;
	width: 33%;
}

.timer-work {
	background: linear-gradient(-180deg, #5ac5ff 0%, #88b0ff 100%);
}

.timer-break {
	background-image: linear-gradient(-180deg, #5eedaa 3%, #50c78f 100%);
}

.timer-button {
	border: 0;
	background: none;
	color: white;
	cursor: pointer;
}

.timer-button > svg {
	width: 24px;
	height: 24px;
}

.timer-button > svg:hover {
	width: 25px;
	height: 25px;
}

/* bad for a11y but looks better ðŸ¤·â€ */
/* good fix: https://medium.freecodecamp.org/how-to-make-your-fancy-svg-button-accessible-83c9172c3c15 */
.timer-button:focus {
	outline: 0;
}

.description > p {
	flex-basis: 100%;
	margin-top: 0.6em;
	font-weight: 200;
	font-size: 1.3rem;
	margin-bottom: 0em;
	text-align: center;
}

.description b {
	font-weight: 550;
}

.todo-item-container {
	background-color: white;
	color: #3a3131;
	/* padding: 1.5em 0.8em; */
	padding: 1em 0.8em;
	display: flex;
	justify-content: space-between;
	min-height: 27px;
	padding-right: 0;
}

.task-info:hover {
	cursor: pointer;
}

.task-info {
	width: 100%;
}

.item-bar {
	display: flex;
	min-height: 27px;
	width: 3px;
	border-radius: 1.5px;
	/* margin-left: 3px; */
}

.active-item-bar {
	display: flex;
	min-height: 27px;
	width: 3px;
	border-radius: 1.5px;
	background: black;
}

.todo-item-container:hover > .item-bar {
	background: rgb(168, 168, 168);
}

.todo-item-note-container {
	box-sizing: border-box;
	background-color: rgb(255, 252, 213);
	display: flex;
	padding: 1em 0.8em;
	margin-top: 0.5em;
	width: 100%;
	border-radius: 5px;
	white-space: pre-wrap;
}

.todo-item-container-left {
	display: flex;
	width: 90%;
}

.todo-item-complete-button {
	border: 0;
	background: none;
	cursor: pointer;
	margin-right: 2px;
	height: 36px;
}

/* bad for a11y but looks better ðŸ¤·â€ */
/* good fix: https://medium.freecodecamp.org/how-to-make-your-fancy-svg-button-accessible-83c9172c3c15 */
.todo-item-complete-button:focus {
	outline: 0;
}

.todo-item-circle {
	border-radius: 100%;
	width: 24px;
	height: 24px;
}

.todo-item-circle-empty {
	box-sizing: border-box;
	border: 2px solid #a2adc1;
}

/* .todo-item-circle-empty:hover {
	background: #a2adc12e;
} */

.todo-item-circle-check {
	background: #64e232;
	color: white;

	/*
    center children vertically and horizontally
    https://stackoverflow.com/questions/25832340/css-flexbox-vertically-horizontally-center-image-without-explicitely-defining-pa
  */
	display: flex;
	justify-content: center; /* align horizontal */
	align-items: center; /* align vertical */
}

.todo-item-circle-check > svg {
	width: 15px;
	height: 15px;
}

.todo-item-task {
	margin-bottom: 8px;
	font-weight: 550;
}

.todo-item-start-session-button {
	display: block;
	outline: 0;
	color: #a2adc1;
	border: 0;
	background: none;
	cursor: pointer;
	height: 36px;
	/* display: none; */
}

.todo-item-start-session-button > svg {
	width: 24px;
	height: 24px;
}

/* .todo-item-start-session-button:hover > svg {
	width: 25px;
	height: 25px;
} */

/* bad for a11y but looks better ðŸ¤·â€ */
/* good fix: https://medium.freecodecamp.org/how-to-make-your-fancy-svg-button-accessible-83c9172c3c15 */
/* .todo-item-start-session-button:focus {
	outline: 0;
} */

.todo-item-container:hover .todo-item-start-session-button {
	display: block;
}

.sessions-completed-container {
	display: flex;
	flex-direction: row;
}

.sessions-line {
	width: 14px;
	height: 3px;
	border-radius: 1px;
	margin-left: 3px;
}

.completed {
	background: #38cc93;
}

.remaining {
	background: #d6d6d6;
}

.additional {
	background: rgb(27, 138, 241);
}

.modal-outer-container {
	background-color: white;
	border-radius: 4px;
	display: flex;
	flex-direction: column;
	/* height: 20em; */
	max-width: 570px !important;
	margin: 15% auto;
	margin-bottom: 0em;

	/* box-sizing: border-box; */
	/* margin-left: auto !important;
	margin-right: auto !important; */
	/* max-width: 600px !important; */
}

.modal-inner-container {
	height: 90%;
	width: 90%;
	margin: 7% auto;
	display: flex;
	flex-direction: column;
	margin-bottom: 0em;
	padding-bottom: 0.5em;
}

.modal-heading {
	color: #8a98af;
}

.modal-close-button {
	background: #4a6ed2;
	color: white;
	border-radius: 0 4px 4px 0;
	/*
    center children vertically and horizontally
    https://stackoverflow.com/questions/25832340/css-flexbox-vertically-horizontally-center-image-without-explicitely-defining-pa
    */
	display: flex;
	justify-content: right; /* align horizontal */
	align-items: center; /* align vertical */
	/* minimum size */
	flex-basis: 2.7em;
	cursor: pointer;
}

.modal-todo-input,
.modal-todo-input-button {
	box-sizing: border-box;
	padding-top: 0.7em;
	padding-bottom: 0.7em;
	font-size: 1.2em;
	font-weight: 550;
	border: 0;
}

.modal-todo-input {
	padding-left: 0.5em;
	background: #ffffff;
	border-radius: 4px 0 0 4px;
	flex-grow: 1;
}
.modal-todo-input::-webkit-input-placeholder {
	color: #8a98af;
}
.modal-todo-input::-ms-input-placeholder {
	color: #8a98af;
}
.modal-todo-input::placeholder {
	color: #8a98af;
}

.modal-todo-input:focus {
	outline: 2px #8a98af;
}

.note {
	margin-top: 1.5em;
	margin-bottom: 1em;
	/* width: 100%; */
}
.task input {
	width: 50%;
}

.note textarea {
	display: block;
	background: #e0e0e0;
	border-radius: 4px;
	width: 100%;
}

.number input {
	background-color: #e0e0e0;
	border-radius: 4px;
	margin-right: 0.3em;
}

.show-note:hover {
	text-decoration: underline;
	cursor: pointer;
}

.modal-footer {
	display: block;
	background-color: #e0e0e0;
	border-bottom-left-radius: 4px;
	border-bottom-right-radius: 4px;
	padding: 0.5em;
}

form {
	display: grid;
}

i:hover {
	cursor: pointer;
}

.flex-wrapper {
	display: flex;
	min-height: 100vh;
	flex-direction: column;
	justify-content: space-between;
	box-sizing: border-box;
	padding: 1.1em;
	max-width: 600px !important;
	margin-left: auto !important;
	margin-right: auto !important;
}

header {
	margin-bottom: 0.9em;
	display: flex;
	color: rgba(0, 0, 0, 0.6);
}

.heading {
	font-size: 2.6em;
	font-weight: 800;
	flex-grow: 70;
}

h1:last-child {
	margin-bottom: 14px;
}

.clear-button {
	color: grey;
	border: 0;
	background: none;
	cursor: pointer;
	margin-right: 0.2em;
	/*
  center children vertically and horizontally
  https://stackoverflow.com/questions/25832340/css-flexbox-vertically-horizontally-center-image-without-explicitely-defining-pa
  */
	display: flex;
	justify-content: center; /* align horizontal */
	align-items: center; /* align vertical */
}

/* bad for a11y but looks better ðŸ¤·â€ */
/* good fix: https://medium.freecodecamp.org/how-to-make-your-fancy-svg-button-accessible-83c9172c3c15 */
.clear-button:focus {
	outline: 0;
}

.clear-button:hover > svg {
	width: 31px;
	height: 31px;
}

.clear-button > svg {
	width: 30px;
	height: 30px;
}

.card {
	border-radius: 9px;
	box-shadow: rgba(0, 0, 0, 0.11) 0px 4px 8px;
	transition: all 0.2s ease-out 0s;
	margin-bottom: 1em;
}

.card:hover {
	box-shadow: rgba(0, 0, 0, 0.13) 0px 6px 20px;
}

.empty-state-container {
	display: flex;
	flex-direction: column;
}

.empty-state-container > svg {
	margin: auto;
	flex-basis: 20em;
}

.empty-state-text {
	text-align: center;
	font-size: 1.2em;
}

